<template>
  <div>
    Hello
  </div>
</template>
<script>
  export default {
    created() {
      if(this.$store.state.user.obj) {
        let practRef = this.$store.state.user.obj.resource.extension.find((ext) => {
          return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference'
        })
        if(practRef) {
          let practitioner = practRef.valueReference.reference.split("/")
          practitioner = practitioner[practitioner.length - 1]
          this.$router.push({
            path: "/resource/view/bwpractitioner/" + practitioner
          })
        } else {
          this.$store.state.message.active = true
          this.$store.state.message.type = 'warning'
          this.$store.state.message.text = 'You dont have a profile on the system'
          this.$store.state.message.timeout = 2000
          this.$router.push({path: '/'})
        }
      }
    }
  }
</script>